      <div class="section">
        <div class="row" v-for="(item, index) in appTop5" :key="index">
          <div class="name">{{ item.name }} 遭受攻击数：{{ item.value }}</div>
          <div class="list">
            <div class="lg" :style="item.percent.high"></div>
            <div class="md" :style="item.percent.medium"></div>
            <div class="sm" :style="item.percent.low"></div>
