<script>
import { api, asyncFn, apiPost } from "@/api/api";
import { fmtNumber } from "@/utils/utils";

import TOP from "@/components/box_top";
import PIE3 from "@/components/box_pie3";
export default {
  name: "appAnalysis",
  components: {
    TOP,
    PIE3
  },
  data() {
    return {
      timer: null,
      attackTotal: 0, // 监控攻击总数
      appList: {
        sum: 0, // 应用数
        lg: 0,
        md: 0,
        sm: 0,
        lg_name: "高危应用",
        md_name: "中危应用",
        sm_name: "低危应用"
      },
      terminalCount: 0, // 终端数
      hackerCount: 0, // 黑客数
      appTop5: [],
      highAttack: 1234,
      chart: null,
      isMore: true, // 还有没有数据
      loading: true, // 应用列表加载中
      pageIndex: 1, // 应用列表页码
      pageSize: 20, // 应用列表每页显示个数
      apps: [] // 应用列表
    };
  },
  computed: {
    highAttackArray() {
      return fmtNumber(this.highAttack, 99999999).split("");
    }
  },
  created() {
    this.loadList();
    this.refreshAPI();
    // 实时刷新 间隔周期30秒
    this.timer = setInterval(() => {
      console.log("实时刷新 间隔周期5分种");
      this.loadList();
      this.refreshAPI();
    }, 5 * 60 * 1000);
  },
  mounted() {
    this.$nextTick(() => {
      this.$refs.box_list.addEventListener("scroll", this.onListScroll);
    });
  },
  beforeDestroy() {
    if (this.timer) clearInterval(this.timer);
    this.timer = null;
  },
  methods: {
    onListScroll(e) {
      // 如果没有在加载数据中 并且 滚动到了底部 并且是第一次或者最近一次获取的数据数量===this.pageSize
      if (
        this.isMore &&
        !this.loading &&
        e.target.scrollHeight - 30 < e.target.scrollTop + e.target.offsetHeight
      ) {
        console.log("用户一旦加载第二页就停止自动刷新");
        if (this.timer) clearInterval(this.timer);
        this.timer = null;
        
        this.loading = true;
        setTimeout(() => {
          this.pageIndex++;
          this.loadList();
        }, 1000);
      }
    },
    async loadList() {
      try {
        let res = await apiPost(api.getSiteInfos, {
          remoteip: this.ip,
          startTime: this.startTime,
          endTime: this.endTime,
          pageIndex: this.pageIndex,
          pageSize: this.pageSize
        });
        if (res.data) {
          res.data.forEach(item => {
            item["color"] = "safe";
            if (item.attack_level.high > 0) {
              item["color"] = "high";
            } else if (item.attack_level.medium > 0) {
              item["color"] = "medium";
            } else if (item.attack_level.low > 0) {
              item["color"] = "low";
            }
          });
          // 始终第一页
          if (this.pageIndex === 1) this.apps = res.data;
          else this.apps = this.apps.concat(res.data);

          // this.apps = this.apps.concat(res.data);
          this.isMore = res.data.length === this.pageSize;
        }
      } catch (error) {
        console.log(error);
      }
      this.loading = false;
    },
    refreshAPI() {
      // 遭受攻击应用TOP5
      asyncFn(api.getAttackApplicationTop, {
        startTime: this.startTime,
        endTime: this.endTime,
        siteIds: this.siteIds
      }).then(data => {
        let tmp = data.slice(0, 5);
        tmp.forEach(item => {
          let high = item.level.high || 0;
          let medium = item.level.medium || 0;
          let low = item.level.low || 0;
          let other = item.value - high - medium - low;
          let fmt = num => Math.round((num / item.value) * 100);
          item.percent = {
            other: {
              display: fmt(other) === 0 ? "none" : "block",
              width: fmt(other) + "%"
            },
            high: {
              display: fmt(high) === 0 ? "none" : "block",
              width: fmt(high) + "%"
            },
            low: {
              display: fmt(low) === 0 ? "none" : "block",
              width: fmt(low) + "%"
            },
            medium: {
              display: fmt(medium) === 0 ? "none" : "block",
              width: fmt(medium) + "%"
            }
          };
        });
        this.appTop5 = tmp;
      });
      // 终端数
      asyncFn(api.getTerminalNum).then(
        i =>
          i &&
          (this.terminalCount = Object.keys(i).length)
      );
      // 黑客人数
      asyncFn(api.hackerNumber).then(
        i => i && (this.hackerCount = i.hackerNum)
      );
      // 高危应用数 应用总数
      asyncFn(api.getApplicationLevel).then(i => {
        this.appList = {
          sum: i.highApplication + i.lowApplication + i.mediumApplication,
          lg: i.highApplication,
          md: i.mediumApplication,
          sm: i.lowApplication,
          lg_name: "高危应用",
          md_name: "中危应用",
          sm_name: "低危应用"
        };
      });
      // 拦截攻击总数数量
      asyncFn(api.interceptNumber).then(i => i && (this.attackTotal = i));
    }
  }
};
</script>
